import React from 'react';
import {Link} from 'gatsby'

const menuItems = [{ url: '/', name: 'Home'}];
if(process.env.NODE_ENV === "development") {
    menuItems.push({url: '/cshirts', name: 'C-Shirts'});
}

const Menu = ()  => {
    return (
        <ul id="menu-primary-menu" className="menu">
            {menuItems.map((item: any) => 
                <li className="menu-item">
                    <Link to={item.url} activeClassName="current-menu-item">{item.name}</Link>
                </li>
            )}
        </ul>
    );
};

export default Menu;